import { SelectChangeEvent } from "@mui/material/Select";
import React from "react";
import { Color, FrontSide } from "three";
import { MeshLambertMaterial } from "three/src/materials/MeshLambertMaterial";
import { CanvasProps } from "../../../hooks/model/root";

window.highlightMaterial = new MeshLambertMaterial({
  transparent: true,
  opacity: 0.6,
  color: 0xff88ff,
  depthTest: false,
});

const useController = (props: CanvasProps) => {
  const [ifcModels, setIfcModels] = React.useState<any[]>([]);
  const [selectedIfcModels, setSelectedIfcModels] = React.useState<any>();
  const [filePaths, setFilePath] = React.useState<any[]>([]);
  const CanvasRef = React.useRef(null);
  const [color, setColor] = React.useState<any>();
  const [selectedMaterial, setSelectedMaterial] = React.useState<any>();
  const [selectedGeometry, setSelectedGeometry] = React.useState<any>();
  const [selectedFaceIndex, setSelectedFaceIndex] = React.useState<any>();
  const [selectedModelID, setSelectedModelID] = React.useState<any>();
  const [selectedId, setSelectedId] = React.useState<any>();
  const [scene, setScene] = React.useState<any>();

  React.useEffect(() => {
    if (props.filePath) {
      const current: any = [...filePaths, { ...props }];
      setFilePath(current);
    }
  }, [props.filePath]);

  const handleChange = (event: SelectChangeEvent) => {
    setSelectedIfcModels(event.target.value);
  };

  const move = (x, z) => {
    if (selectedIfcModels && scene) {
      const index = scene.children.findIndex(
        (i) => i.uuid === selectedIfcModels
      );
      const model = scene.children[index];
      model.position.setX(model.position.x + x);
      model.position.setZ(model.position.z + z);
    }
  };

  const changeColor = async (e) => {
    setColor(e);
    if (
      window.manager !== undefined &&
      scene !== undefined &&
      selectedModelID !== undefined &&
      selectedId !== undefined
    ) {
      window.manager.removeSubset(selectedModelID, window.highlightMaterial);
      const material = new MeshLambertMaterial({
        transparent: false,
        opacity: 1,
        color: 0xffffff + e.value,
        depthTest: false,
        depthWrite: false,
        alphaTest: 1,
        side: FrontSide,
      });
      window.manager.createSubset({
        modelID: selectedModelID,
        ids: [selectedId],
        material,
        scene,
        removePrevious: true,
      });
    }
  };

  return {
    CanvasRef,
    selectedGeometry,
    selectedMaterial,
    selectedFaceIndex,
    selectedModelID,
    setSelectedMaterial,
    setSelectedGeometry,
    setSelectedFaceIndex,
    setSelectedModelID,
    setScene,
    setSelectedId,
    setIfcModels,
    move,
    filePaths,
    color,
    changeColor,
    handleChange,
    ifcModels,
    selectedIfcModels,
  };
};

export default useController;
