export const classes = {
  root: `${PREFIX}-root`,
  info: `${PREFIX}-info`,
  action: `${PREFIX}-action`,
};

const Root = styled("section")(({ theme }) => ({
  [`&.${classes.root}`]: {
    width: "100%",
    padding: 0,
    margin: 0,
    "& canvas": {
      width: "100% !important",
      height: "calc(100vh - 88px) !important",
      outline: "none !important",
    },
  },
  [`& .${classes.info}`]: {
    height: "65px",
  },
  [`& .${classes.action}`]: {
    height: "65px",
  },
}));

export default Root;
