import React, { ReactElement } from "react";
import Root, { classes } from "./common/style";
import ErrorBoundry from "../ErrorBoundry";
import { CanvasProps } from "../../hooks/model/root";
import useController from "./common/useController";
import IFCContainer from "./../IFCContainer";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { ColorPicker } from "material-ui-color";
import Grid from "@mui/material/Grid";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

const diff = 0.1;
const Home: React.FC<CanvasProps> = (props: CanvasProps): ReactElement => {
  const {
    CanvasRef,
    selectedGeometry,
    selectedMaterial,
    selectedFaceIndex,
    selectedModelID,
    setSelectedMaterial,
    setSelectedGeometry,
    setSelectedFaceIndex,
    setSelectedModelID,
    setScene,
    setSelectedId,
    setIfcModels,
    move,
    filePaths,
    color,
    changeColor,
    handleChange,
    ifcModels,
    selectedIfcModels,
  } = useController(props);
  return (
    <ErrorBoundry>
      <Root className={classes.root} data-testid="Canvas__page-id">
        <Grid container spacing={2} className={classes.info}>
          <Grid item xs={3} hidden={!selectedGeometry}>
            <ColorPicker
              value={`#${color?.hex || "FFFFFF"}`}
              onChange={(e) => {
                changeColor(e);
              }}
              disableTextfield
              disableAlpha
            />
          </Grid>
          <Grid item xs={3}>
            <div>Selected Geometry</div>
            <div>{selectedGeometry?.uuid}</div>
          </Grid>
          <Grid item xs={3}>
            <div>Selected Material Color</div>
            <div>{selectedMaterial?.uuid}</div>
          </Grid>
        </Grid>
        <Grid container spacing={2} className={classes.info}>
          <Grid item xs={3} hidden={!ifcModels.length}>
            <FormControl variant="standard" sx={{ m: 1, minWidth: 200 }}>
              <InputLabel>IFC Model</InputLabel>
              <Select
                label="IFC Model"
                value={selectedIfcModels || ""}
                onChange={handleChange}
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                {ifcModels &&
                  ifcModels.length &&
                  ifcModels.map((item) => {
                    return (
                      <MenuItem key={item.uuid} value={item.uuid}>
                        {item.fileName}
                      </MenuItem>
                    );
                  })}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={7} hidden={!selectedIfcModels}>
            <button
              onClick={() => {
                move(diff, 0);
              }}
            >
              move x + 1
            </button>
            <button
              onClick={() => {
                move(-diff, 0);
              }}
            >
              move x - 1
            </button>
            <button
              onClick={() => {
                move(0, diff);
              }}
            >
              move z + 1
            </button>
            <button
              onClick={() => {
                move(0, -diff);
              }}
            >
              move z - 1
            </button>
          </Grid>
        </Grid>

        <Canvas
          ref={CanvasRef}
          camera={{
            far: 500,
            near: 1,
            zoom: 1,
            position: [1, 8, 20],
            rotation: [-0.4, 0.55, 0.2],
          }}
        >
          <ambientLight intensity={0.1} color="white" />
          <directionalLight color="white" position={[40, 40, 100]} />
          <gridHelper args={[100, 100]} />
          {filePaths &&
            filePaths.length &&
            filePaths.map((item) => (
              <React.Suspense key={item.filePath} fallback={null}>
                <IFCContainer
                  filePath={item.filePath}
                  setSelectedMaterial={setSelectedMaterial}
                  setSelectedGeometry={setSelectedGeometry}
                  setSelectedFaceIndex={setSelectedFaceIndex}
                  setSelectedModelID={setSelectedModelID}
                  setScene={setScene}
                  setSelectedId={setSelectedId}
                  setIfcModels={setIfcModels}
                  fileName={item.fileName}
                />
              </React.Suspense>
            ))}
          <axesHelper args={[20]} />
          <OrbitControls
            enablePan={true}
            enableZoom={true}
            enableRotate={true}
          />
        </Canvas>
      </Root>
    </ErrorBoundry>
  );
};

export default Home;
