import { styled } from "@mui/material/styles";

const PREFIX = `${process.env.REACT_APP_PREFIX_STYLE}_home`;
export const classes = {
  root: `${PREFIX}-root`,
  group: `${PREFIX}-group`,
  label: `${PREFIX}-label`,
  file: `${PREFIX}-file`,
};

const Root = styled("section")(({ theme }) => ({
  [`&.${classes.root}`]: {
    width: "100%",
    padding: theme.spacing(2),
    margin: 0,
  },
  [`& .${classes.group}`]: {
    padding: 0,
    margin: 0,
  },
  [`& .${classes.label}`]: {
    display: "flex",
    marginLeft: theme.spacing(1),
    "& strong": {
      marginRight: theme.spacing(1),
    },
  },
  [`& .${classes.file}`]: {
    visibility: "hidden",
    width: "0px",
    display: "inline-block",
  },
}));

export default Root;
