import React from "react";
import { FallbackErrorProps } from "../../../hooks/model/root";
import { putService } from "../../../hooks/service";

const useController = (props: FallbackErrorProps) => {
  const [errorName, setErrorName] = React.useState<string>();
  const [errorMessage, setErrorMessage] = React.useState<string>();
  const [errorStack, setErrorStack] = React.useState<string>();
  React.useEffect(() => {
    if (props.error) {
      //putService("/error", { ...props.error }).catch((e) => {});
      setErrorMessage(props.error.message);
      setErrorName(props.error.name);
      setErrorStack(props.error.stack);
    }
  }, [props.error]);

  return { errorMessage, errorName, errorStack };
};

export default useController;
