import React, { ReactElement } from "react";
import Grid from "@mui/material/Grid";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";
import Button from "@mui/material/Button";
import useController from "./common/useController";
import Root, { classes } from "./common/style";
import ErrorBoundry from "../ErrorBoundry";
import Canvas from "../Canvas";

const Home: React.FC = (props): ReactElement => {
  const {
    store,
    toggleColorMode,
    mode,
    fileInput,
    handleClickOpen,
    onFileChange,
    filePath,
    fileName,
  } = useController(props);

  return (
    <ErrorBoundry>
      <Root className={classes.root} data-testid="Home__page-id">
        <FormGroup>
          <input
            className={classes.file}
            ref={fileInput}
            id="file-input"
            type="file"
            onChange={onFileChange}
            accept=".ifc, .ifcXML, .ifcZIP"
          />
          <Grid container spacing={0}>
            <Grid item xs={6}>
              <FormControlLabel
                className={classes.group}
                control={
                  <Switch
                    data-testid="Home__switch-id"
                    checked={mode === "light"}
                    onChange={toggleColorMode}
                  />
                }
                label={
                  <span className={classes.label}>
                    <strong>{store.theme}</strong>
                    {mode === "light" ? (
                      <Brightness7Icon />
                    ) : (
                      <Brightness4Icon />
                    )}
                  </span>
                }
              />
            </Grid>
            <Grid item xs={6}>
              <FormControlLabel
                control={
                  <Button
                    size="small"
                    variant="contained"
                    color="secondary"
                    onClick={(e) => handleClickOpen()}
                  >
                    Select IFC File
                  </Button>
                }
                label={
                  <span className={classes.label}>
                    {fileInput?.current?.files &&
                      fileInput?.current?.files[0] &&
                      fileInput?.current?.files[0].name}
                  </span>
                }
              />
            </Grid>
          </Grid>
        </FormGroup>
        <Canvas filePath={filePath} fileName={fileName} />
      </Root>
    </ErrorBoundry>
  );
};

export default Home;
