import React, { ReactElement } from "react";
import Root, { classes } from "./common/style";
import { FallbackErrorProps } from "../../hooks/model/root";
import useController from "./common/useController";

const FallbackError: React.FC<FallbackErrorProps> = (
  props: FallbackErrorProps
): ReactElement => {
  const { errorMessage, errorName, errorStack } = useController(props);
  return (
    <Root className={classes.root} data-testid="FallbackError__page-id">
      <div>
        <strong>{errorName}</strong>
      </div>
      <div>{errorMessage}</div>
      <div>{errorStack}</div>
    </Root>
  );
};

export default FallbackError;
