import React, { useRef } from "react";
import { useBVH } from "@react-three/drei";
import { useLoader, useThree } from "@react-three/fiber";
import { Intersection } from "three";
import { IFCModel } from "web-ifc-three/IFC/components/IFCModel";
import { IFCLoader } from "web-ifc-three/IFCLoader";
import {
  acceleratedRaycast,
  computeBoundsTree,
  disposeBoundsTree,
} from "three-mesh-bvh";

export interface IFCContainerProps {
  filePath: string;
  fileName: string;
  setSelectedMaterial: Function;
  setSelectedGeometry: Function;
  setSelectedFaceIndex: Function;
  setSelectedModelID: Function;
  setScene: Function;
  setSelectedId: Function;
  setIfcModels: Function;
}

const useController = (props: IFCContainerProps) => {
  let {
    filePath,
    fileName,
    setSelectedMaterial,
    setSelectedGeometry,
    setSelectedFaceIndex,
    setSelectedModelID,
    setIfcModels,
    setScene,
    setSelectedId,
  } = props;

  const scene = useThree((state) => state.scene);
  const highlightedModelId = useRef(-1);
  const mesh = useRef(null!);
  useBVH(mesh);

  const ifc = useLoader(IFCLoader, filePath, (loader) => {
    window.manager &&
      window.manager.removeSubset(
        highlightedModelId.current,
        window.highlightMaterial
      );
    window.ifcLoader = loader as IFCLoader;
    const { ifcManager } = window.ifcLoader;
    ifcManager.setWasmPath("/");
    window.manager = ifcManager;
    window.manager.setupThreeMeshBVH(
      computeBoundsTree,
      disposeBoundsTree,
      acceleratedRaycast
    );
  });

  React.useEffect(() => {
    setScene(scene);
    setIfcModels((c) => [...c, { ...ifc, filePath, fileName }]);
  }, []);

  function handleDblClick(event: Intersection<IFCModel>) {
    window.manager.removeSubset(
      highlightedModelId.current,
      window.highlightMaterial
    );
    highlight(event);
  }

  async function highlight(intersection: Intersection<IFCModel>) {
    const { faceIndex } = intersection;
    const { modelID, geometry } = intersection.object;
    const id = window.manager.getExpressId(geometry, faceIndex || 1);
    highlightedModelId.current = modelID;
    setSelectedGeometry(geometry);
    setSelectedFaceIndex(faceIndex);
    setSelectedModelID(modelID);
    setSelectedMaterial(modelID);
    setSelectedId(id);
    window.manager.createSubset({
      modelID,
      ids: [id],
      material: window.highlightMaterial,
      scene,
      removePrevious: true,
    });
  }

  return {
    mesh,
    ifc,
    handleDblClick,
  };
};

export default useController;
