import React from "react";
import { useContext } from "../../../hooks/provider";
import { ActionType } from "../../../hooks/reducer/util/ActionType";

const useController = (props: any) => {
  const [store, dispacth] = useContext();
  const fileInput = React.useRef<any>(null);
  const [error, setError] = React.useState<any>(undefined);
  const [filePath, setFilePath] = React.useState<string>(null!);
  const [fileName, setFileName] = React.useState<string>(null!);
  const [mode, setMode] = React.useState<"light" | "dark">(
    store.theme as "light" | "dark"
  );
  const toggleColorMode = () => {
    setMode((prevMode) => (prevMode === "light" ? "dark" : "light"));
  };
  React.useEffect(() => {
    if (mode) {
      dispacth({ type: ActionType.SET_THEME, data: { theme: mode } });
    }
  }, [dispacth, mode]);

  const onFileChange = () => {
    if (fileInput.current.files && fileInput.current.files[0]) {
      if (fileInput.current.files[0].size <= 5242880) {
        const ifcURL = URL.createObjectURL(fileInput.current.files[0]);
        setFilePath(ifcURL);
        setFileName(fileInput.current.files[0].name);
      } else {
        setError("File size is too big. Please select file with size <= 5 MB");
      }
    }
  };

  const handleClickOpen = () => {
    setError(undefined);
    fileInput?.current?.click();
  };

  return {
    store,
    toggleColorMode,
    mode,
    fileInput,
    onFileChange,
    handleClickOpen,
    error,
    filePath,
    fileName,
  };
};

export default useController;
